.screen-services-top {
	padding: 85px 0 15px;
	@include r(1200) {
		padding: 65px 0 10px; }
	@include r(1000) {
		padding: 45px 0 5px; }
	@include r(900) {
		padding: 40px 0 0; }
	.container {
		padding: 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: center; } }


.service-top {
	position: relative;
	box-sizing: border-box;
	margin-bottom: 35px;
	padding: 0 15px;
	width: calc(100% / 3);
	overflow: hidden;
	@include r(1200) {
		margin-bottom: 25px; }
	@include r(1000) {
		margin-bottom: 15px; }
	@include r(800) {
		margin-bottom: 10px;
		padding: 0 5px; }
	@include r(620) {
		width: calc(100% / 2); }
	@include r(400) {
		width: 100%; }
	&-overlay {
		position: absolute;
		top: 0;
		left: 15px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: calc(100% - 30px);
		height: 100%;
		background-color: rgba($purp-light, .6);
		transition: .25s;
		@include r(800) {
			left: 5px;
			width: calc(100% - 10px); }
		&__text {
			margin-bottom: 10px;
			text-align: center;
			font-family: $proxima;
			@include fs(28);
			font-weight: 400;
			color: $light;
			@include r(1200) {
				@include fs(24); }
			@include r(1000) {
				margin-bottom: 5px;
				@include fs(20); }
			@include r(800) {
				@include fs(18); } }
		.btn {
			width: calc(100% - 20px);
			@include fs(26);
			@include r(1200) {
				@include fs(20); }
			@include r(1000) {
				@include fs(18); }
			@include r(800) {
				@include fs(16); } } }
	&_top {
		.service-top-overlay {
			transform: translateY(-100%); }
		&:hover {
			.service-top-overlay {
				transform: translateY(0%); } } }
	&_bottom {
		display: flex;
		flex-direction: column;
		.service-top-overlay {
			transform: translateY(+100%); }
		@include r(620) {
			flex-direction: column-reverse; }
		&:hover {
			.service-top-overlay {
				transform: translateY(0%); } } }
	&__title {
		position: relative;
		z-index: 2;
		padding: 13px 10px;
		text-align: center;
		font-family: $agopus;
		@include fs(25);
		@include ls(50);
		font-weight: 400;
		text-transform: uppercase;
		color: $light;
		background-color: $purp;
		@include r(1200) {
			@include fs(20); }
		@include r(1000) {
			@include fs(18); }
		@include r(900) {
			@include fs(15); }
		@include r(800) {
			display: flex;
			justify-content: center;
			align-items: center;
			min-height: 68px; } }
	&-img {
		display: block;
		width: 100%;
		height: 100%;
		&__img {
			display: inline-block;
			vertical-align: top;
			max-width: 100%;
			width: 100%;
			// height: auto
			height: 100%;
			object-fit: cover; } }
	&-text {
		margin-bottom: 35px;
		padding: 0 15px;
		width: 100%;
		text-align: center;
		font-family: $proxima;
		@include fs(37);
		font-weight: 600;
		color: #460d52;
		@include r(1200) {
			margin-bottom: 25px;
			@include fs(30); }
		@include r(1000) {
			margin-bottom: 15px;
			@include fs(26); }
		@include r(900) {
			@include fs(24); }
		@include r(800) {
			margin-bottom: 10px;
			@include fs(21); }
		@include r(620) {
			order: -1; } } }
