.screen-lid {
	padding: 75px 0 85px;
	@include pseudo;
	@include image('cor-gradient-bg.jpg');
	@include cover;
	@include r(1200) {
		padding: 45px 0 55px; }
	@include r(1000) {
		padding: 35px 0 45px; }
	@include r(900) {
		padding: 30px 0 40px; }
	&::before {
		z-index: 1;
		@include coverdiv;
		background-color: rgba($purp-dark, .7); }
	.container {
		position: relative;
		z-index: 2;
		text-align: center;
		color: $light; }
	&__title {
		margin: 0 auto 40px;
		text-align: center;
		font-family: $proxima;
		@include fs(36);
		font-weight: 600;
		color: $light;
		@include r(1200) {
			margin-bottom: 25px;
			@include fs(30); }
		@include r(1000) {
			margin-bottom: 20px;
			@include fs(26); }
		@include r(900) {
			margin-bottom: 15px;
			@include fs(24); }
		@include r(800) {
			@include fs(22); }
		@include r(400) {
			@include fs(20); } }
	.btn {
		margin-bottom: 40px;
		min-width: 345px;
		@include r(1200) {
			margin-bottom: 25px;
			min-width: 310px; }
		@include r(1000) {
			margin-bottom: 20px; }
		@include r(800) {
			padding-left: 15px;
			padding-right: 15px;
			min-width: auto; } }
	&__text {
		margin: 0 auto;
		width: 60%;
		text-align: center;
		@include fs(30);
		font-weight: 400;
		@include r(1200) {
			width: 70%;
			@include fs(24); }
		@include r(1000) {
			@include fs(22); }
		@include r(900) {
			@include fs(20); }
		@include r(800) {
			@include fs(18); }
		@include r(400) {
			width: 100%; } } }

