.hero {
	position: relative;
	margin-top: 90px;
	display: flex;
	@include r(1200) {
		margin-top: 80px; }
	@include r(1000) {
		margin-top: 60px; }
	&-content {
		position: relative;
		z-index: 2;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		width: 100%;
		min-height: calc(100vh - 90px);
		margin: auto auto 0;
		@include r(1200) {
			min-height: calc(100vh - 80px); }
		@include r(1000) {
			min-height: calc(100vh - 60px); }
		&__title {
			box-sizing: border-box;
			padding: 35px 0;
			width: 70%;
			font-family: $proxima;
			@include fs(50);
			font-weight: 700;
			color: $light;
			@include r(1200) {
				padding: 25px 0;
				@include fs(40); }
			@include r(1000) {
				padding: 15px 0;
				@include fs(34); }
			@include r(900) {
				padding: 75px 0 15px;
				@include fs(32); }
			@include r(800) {
				@include fs(30); }
			@include r(700) {
				width: 80%;
				@include fs(26); }
			@include r(600) {
				width: 100%; }
			@include r(400) {
				@include fs(20); } }
		&-form {
			padding: 20px 0 30px;
			width: 100%;
			background-color: rgba($purp-dark, .7);
			@include r(1200) {
				padding: 15px 0 25px; }
			@include r(1000) {
				padding: 10px 0 20px; }
			@include r(900) {
				padding: 10px 0 15px; }
			&__text {
				display: block;
				margin-bottom: 10px;
				font-family: $proxima;
				@include fs(30);
				font-weight: 600;
				color: $light;
				@include r(1200) {
					@include fs(26); }
				@include r(1000) {
					@include fs(22); }
				@include r(900) {
					@include fs(20); }
				@include r(800) {
					margin-bottom: 5px;
					@include fs(18); }
				@include r(700) {
					@include fs(16); } } } }
	&-video {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		overflow: hidden;
		@include image('video-poster.jpg');
		@include cover;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			width: 100%;
			height: 100%;
			background-color: rgba($purp-dark, .7); }
		&__video {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: 0;
			min-width: 105%;
			min-height: 105%;
			width: auto;
			height: auto;
			transform: translate(-50%, -50%); } } }

.hero-form {
	display: flex;
	justify-content: space-between;
	@include r(700) {
		flex-wrap: wrap; }
	& > * {
		flex: 1;
		margin-right: 30px;
		@include r(900) {
			margin-right: 15px; }
		@include r(700) {
			flex: auto;
			margin-right: 0;
			width: calc(50% - 10px); }
		@include r(400) {
			margin: 0 0 10px;
			width: 100%; }
		&.input {
			margin-bottom: 10px;
			height: 35px;
			&:not(:last-of-type) {
				margin-right: 10px;
				@include r(400) {
					margin-right: 0; } } } }
	.btn {
		margin-right: 0;
		@include r(700) {
			width: 100%;
			padding: 0;
			line-height: 35px; } } }
