.header {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 20;
	padding: 10px 0;
	width: 100%;
	text-align: center;
	background-color: $light;
	transform: translate3d(0,0,0);
	transition: .25s;
	transition-timing-function: ease-out;
	@include r(1000) {
		padding: 5px 0; }
	@include r(800);
	&.is-hide {
		transform: translateY(-100%); }
	.container {
		display: flex;
		justify-content: space-between;
		align-items: flex-end; }
	&-logo {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 20px;
		padding-bottom: 5px;
		min-width: 145px;
		width: 145px;
		@include r(1200) {
			width: 125px;
			min-width: 125px; }
		@include r(1000) {
			width: 100px;
			min-width: 100px; }
		@include r(900) {
			margin-right: 10px; }
		&__img {
			max-width: 100%;
			width: 100%;
			height: auto; } }
	&-nav {
		display: flex;
		justify-content: center;
		align-items: center;
		&-list {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 0;
			padding: 0;
			width: 100%;
			list-style: none;
			@include r(800) {
				flex-direction: column;
				justify-content: flex-start; }
			&-li {
				@include r(800) {
					margin-bottom: 15px; }
				&__link {
					display: block;
					box-sizing: border-box;
					padding: 10px 25px;
					text-align: center;
					font-family: $proxima;
					@include fs(22);
					font-weight: 400;
					text-transform: uppercase;
					color: $blue-dark;
					text-decoration: none;
					transition: .25s;
					@include r(1200) {
						padding: 10px 12px;
						@include fs(20); }
					@include r(1000) {
						padding: 5px 7px;
						@include fs(18); }
					&:hover {
						color: $pink;
						text-decoration: none; } } } } }
	&-info {
		text-align: center;
		@include r(800) {
			margin-top: 20px; }
		&__text {
			text-align: center;
			font-family: $proxima;
			@include fs(20);
			font-weight: 700;
			color: $blue-dark;
			@include r(1200) {
				@include fs(18); }
			@include r(1000) {
				@include fs(16);
				@include lh(16, 16); }
			@include r(900) {
				@include fs(14); } }
		&__link {
			display: inline-block;
			vertical-align: middle;
			font-family: $proxima;
			@include fs(22);
			font-weight: 400;
			color: $blue-dark;
			text-decoration: none;
			transition: .25s;
			@include r(1200) {
				@include fs(20); }
			@include r(1000) {
				@include fs(18);
				@include lh(18, 18); }
			&:hover {
				color: $pink;
				text-decoration: none; } } } }

.mobile-menu {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	@include r(800) {
		position: absolute;
		top: 60px;
		left: 0;
		z-index: -1;
		display: block;
		box-sizing: border-box;
		padding-top: 20px;
		padding-bottom: 40px;
		width: 100%;
		height: calc(100vh - 60px);
		background-color: $light;
		overflow-y: auto;
		transform: translateY(-100%);
		opacity: 0;
		transition: .25s;
		&.is-show {
			opacity: 1;
			transform: translateY(0%); } } }

.btn-mobile-menu {
	display: none;
	@include r(800) {
		display: block;
		align-self: center;
		width: 40px;
		height: 40px;
		background-color: transparent; } }


