.screen {
	padding: 45px 0 75px;
	@include r(1200) {
		padding: 35px 0 65px; }
	@include r(1000) {
		padding: 25px 0 45px; }
	@include r(900) {
		padding: 20px 0 40; }
	&-title {
		margin: 0 auto 60px;
		padding-bottom: 10px;
		width: auto;
		text-align: center;
		font-family: $proxima;
		@include fs(33);
		font-weight: 600;
		color: $blue-dark;
		@include r(1200) {
			margin-bottom: 40px;
			@include fs(30); }
		@include r(1000) {
			@include fs(26);
			margin-bottom: 30px; }
		@include r(900) {
			margin-bottom: 20px;
			@include fs(24); }
		@include r(700) {
			@include fs(22); }
		&__content {
			display: inline-block;
			vertical-align: middle;
			@include pseudo;
			&::before {
				top: calc(100% + 6px);
				width: 100%;
				height: 4px;
				@include g(#3d163b, #872c4c);
				opacity: .25;
				@include r(1200) {
					height: 3px; } } } } }
