.cor-parralax {
	padding: 120px 0;
	text-align: center;
	background: $purp;
	overflow: hidden;
	@include pseudo;
	@include r(1200) {
		padding: 90px 0; }
	@include r(1000) {
		padding: 60px 0; }
	@include r(900) {
		padding: 50px 0; }
	@include r(800) {
		padding: 40px 0; }
	&::before {
		top: 0;
		left: 0;
		z-index: 2;
		width: 100%;
		height: 100%;
		background-color: rgba($purp-dark, .7); }
	&_top {
		font-family: $proxima;
		@include fs(40);
		font-weight: 400;
		@include r(1200) {
			@include fs(30); }
		@include r(1000) {
			@include fs(24); }
		@include r(900) {
			@include fs(22); }
		@include r(800) {
			@include fs(20); }
		p:not(:last-of-type) {
			margin-bottom: 30px;
			@include r(1200) {
				margin-bottom: 20px; }
			@include r(900) {
				margin-bottom: 15px; }
			@include r(800) {
				margin-bottom: 10px; } }
		.cor-parralax-rellax {
			@include image('cor-parll-1-2.jpg'); } }
	&_bottom {
		.cor-parralax {
			&__title {
				margin-bottom: 35px;
				font-family: $proxima;
				@include fs(36);
				font-weight: 600;
				color: $light;
				@include r(1200) {
					margin-bottom: 25px;
					@include fs(30); }
				@include r(1000) {
					margin-bottom: 20px;
					@include fs(28); }
				@include r(900) {
					@include fs(26); }
				@include r(800) {
					@include fs(24); }
				@include r(600) {
					@include fs(22); } }
			&-list {
				margin: 0 auto 40px;
				width: 60%;
				text-align: left;
				list-style-type: decimal;
				@include r(1200) {
					margin-bottom: 30px;
					width: 70%; }
				@include r(1000) {
					margin-bottom: 25px;
					width: 80%; }
				@include r(400) {
					box-sizing: border-box;
					padding-left: 15px;
					width: 100%; }
				&__li {
					margin-bottom: 20px;
					@include fs(22);
					font-weight: 400;
					@include r(1200) {
						margin-bottom: 15px;
						@include fs(20); }
					@include r(1000) {
						margin-bottom: 10px;
						@include fs(18); }
					@include r(600) {
						@include fs(16); } } }
			&-rellax {
				top: 0;
				min-height: 250%;
				@include image('cor-parll-2-2.jpg');
				@include r(700) {
					top: 0!important;
					min-height: 350%; } }
			&__text {
				@include fs(33);
				font-weight: 600;
				@include r(1200) {
					@include fs(26); }
				@include r(1000) {
					@include fs(24); }
				@include r(900) {
					@include fs(22); }
				@include r(800) {
					@include fs(20); }
				@include r(600) {
					@include fs(18); } } }
		.btn {
			margin-bottom: 50px;
			min-width: 340px;
			@include r(1200) {
				margin-bottom: 40px;
				min-width: 300px; }
			@include r(1000) {
				margin-bottom: 30px; } } }


	.container {
		position: relative;
		z-index: 2;
		color: $light; } }

.cor-parralax-rellax {
	position: absolute;
	top: -25%;
	left: 0;
	width: 100%;
	min-height: 200%;
	text-align: center;
	@include cover;
	background-position: center;
	@include r(1000) {
		min-height: 300%; }
	@include r(700) {
		top: 25%; }
	img {
		display: inline-block;
		vertical-align: middle;
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto; } }

