.features {
	&-row {
		display: flex;
		@include r(700) {
			flex-wrap: wrap; } }
	&-col {
		display: flex;
		flex-direction: column;
		box-sizing: border-box;
		@include r(700) {
			width: 100%;
 }			// flex-wrap: wrap
		&_img {
			overflow: hidden;
			img {
				object-fit: cover;
				width: 100%;
				height: 100%; } }
		&_text {
			justify-content: center;
			min-height: 210px;
			padding: 25px;
			font-family: $proxima;
			@include fs(22);
			font-weight: 300;
			color: $dark;
			@include r(1200) {
				padding: 20px;
				@include fs(20); }
			@include r(1000) {
				padding: 15px;
				@include fs(18); }
			@include r(900) {
				min-height: 160px;
				padding: 10px;
				@include fs(16); }
			@include r(800) {
				min-height: 150px;
				@include fs(15); }
			@include r(700) {
				min-height: 100px;
				text-align: center;
				@include fs(16); }
			.bold {
				display: contents; } }
		&_hero {
			flex-basis: 60%;
			justify-content: center;
			align-items: center;
			padding: 25px;
			text-align: center;
			font-family: $proxima;
			@include fs(26);
			font-weight: 400;
			color: $light;
			@include image('cor-gradient-bg.jpg');
			@include cover;
			@include pseudo;
			@include r(1200) {
				padding: 20px;
				@include fs(22); }
			@include r(1000) {
				padding: 15px;
				@include fs(20); }
			@include r(900) {
				padding: 10px;
				@include fs(18); }
			@include r(800) {
				@include fs(16); }
			@include r(700) {
				padding: 15px;
				min-height: 180px;
				@include fs(18); }
			&-content {
				position: relative;
				z-index: 2;
				width: 100%; }
			&::before {
				z-index: 1;
				width: 100%;
				height: 100%;
				background-color: rgba($purp-dark, .7); } }
		&_47 {
			width: 47%;
			@include r(700) {
				width: 50%; }
			@include r(400) {
				flex-wrap: wrap;
				width: 100%; } }
		&_43 {
			width: 43%;
			@include r(700) {
				width: 100%; }
			@include r(400) {
				flex-wrap: wrap; } }
		&_29 {
			width: 29%;
			@include r(700) {
				flex-direction: row;
				&.mobile-hidden {
					display: none; }
				.features-col_text {
					width: 50%; }
				.features-col_img {
					order: -1;
					width: 50%; } }
			@include r(400) {
				width: 100%;
				flex-wrap: wrap;
				.features-col_text {
					width: 100%; }
				.features-col_img {
					order: -1;
					width: 100%; } } }

		&_28 {
			width: 28%;
			@include r(700) {
				flex-direction: row;
				.features-col_text {
					width: 50%; }
				.features-col_img {
					width: 50%; } }
			@include r(400) {
				flex-wrap: wrap;
				width: 100%;
				.features-col_text {
					width: 100%; }
				.features-col_img {
					order: -1;
					width: 100%; } } }
		&_24 {
			width: 24%;
			@include r(700) {
				width: 50%; }
			@include r(400) {
				width: 100%; } } } }
