.overlay {
	position: fixed;
	top: 0;
	left: 0;
	z-index: -1;
	width: 100%;
	height: 100vh;
	overflow: auto;
	background-color: rgba($purp-dark, .7);
	opacity: 0;
	transition: .25s;
	&.is-active {
		opacity: 1;
		z-index: 100; }
	&__close {
		position: absolute;
		top: 15px;
		right: 15px;
		width: 40px;
		height: 40px;
		font-size: 0;
		cursor: pointer;
		border: 0;
		background-color: transparent;
		outline: none;
		&::before,
		&::after {
			content: '';
			position: absolute;
			width: 30px;
			height: 3px;
			background-color: $light;
			transition: .25s; }
		&::before {
			top: 50%;
			left: 0;
			transform: rotate(45deg); }
		&::after {
			top: 50%;
			left: 0;
			transform: rotate(-45deg); }
		&:hover::before,
		&:hover::after {
			background-color: $grad-end; } }
	&-content {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 50px 0;
		width: 100%;
		min-height: 100vh;
		box-sizing: border-box;
		&-form {
			position: relative;
			display: none;
			flex-direction: column;
			padding: 22px 40px 40px;
			width: 444px;
			max-width: calc(100% - 30px);
			box-sizing: border-box;
			background-color: $light;
			opacity: 0;
			transition: .25s;
			@include r(1200) {
				padding: 22px 35px 40px; }
			@include r(1000) {
				padding: 22px 30px 35px; }
			@include r(600) {
				padding: 22px 25px 30px; }
			@include r(400) {
				padding: 22px 20px 20px; }
			&.is-active {
				display: flex;
				opacity: 1; }
			&__title {
				margin-bottom: 25px;
				text-align: center;
				font-size: 28px;
				@include lh(28, 30);
				font-weight: 600;
				color: $purp-dark;
				@include r(1200) {
					@include fs(26); }
				@include r(1000) {
					@include fs(24); }
				@include r(800) {
					@include fs(22); }
				@include r(600) {
					@include fs(20); }
				@include r(400) {
					@include fs(18); } }

			.input {
				margin-bottom: 15px;
				height: 40px;
				border-color: $purp-dark;
				&.input-date {
					margin-bottom: 10px; }
				&:hover {
					border-color: $grad-start; }
				&:focus {
					border-color: $pink; } }
			.datepicker-inline {
				margin-bottom: 15px; }


			textarea.input {
				padding: {
					top: 6px;
					bottom: 6px; }
				height: 120px;
				resize: none; }
			.btn {
				align-self: center; } } } }
